import React from 'react'
import { connect } from 'react-redux'
import { turn } from './actionCreators'
import './home.css'
const Login = (props) => {
    console.log("查看：", props.userinfo);
    // props.dispatch(turn());
    // console.log("123:",state);
    const logn = () => {
        props.dispatch(turn());
        localStorage.setItem('userinfo', props.userinfo)
        if (props.userinfo !== '') {
            props.history.replace(props.location.state)
        }
        // }


    }
    
    return (
        <div className="div-Login">
            <input className="input-btn" type="text" name="name" placeholder="用户名"/>
            <br />
            <input className="input-btn" type="password" name="psd" placeholder="登录密码"/>
            <br />
            <button onClick={logn} className="Login-button">登录</button>
        </div>
    )
}

function mapStateToProps(state) {
    //得到state中的值
    return {
        userinfo: state.Loginreducer.userinfo
    }
}

export default connect(mapStateToProps)(Login)