import React from 'react'
import { NavBar, Icon } from 'antd-mobile';
import Bottom from './Bottom'
import { connect } from 'react-redux'
import './home.css'
const Book = (props) => {
    // let img = 'props.datalist[0].img';
    let name = props.datalist[0].title
    console.log(props.datalist);
    return (
        <div className="box">
            <NavBar
                mode="light"
                icon={<Icon type="left" />}
                onLeftClick={() => props.history.goBack()}
                rightContent={[
                    <Icon key="0" type="search" style={{ marginRight: '16px' }} />,
                    <Icon key="1" type="ellipsis" />,
                ]}
            >{name}</NavBar>
            <div className="box2"> 
                <img className="box1" src={props.datalist[0].img} />
                <p>{props.datalist[0].title}</p>
                <p>￥{props.datalist[0].price}</p>
            </div>

            <Bottom {...props} />
        </div>
    )
}
const mapStateToProps = (state) => ({
    datalist: state.Bookreducer.datalist
})

export default connect(mapStateToProps)(Book)