import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
// import {collection} from './actionCreators'
import './home.css'
const Bottom = (props) => {
    const [isShow, setShow] = useState('none');
    const [isAppare, setAppare] = useState('block');
    console.log("组件props:", props)
    const compare = () => {
        localStorage.getItem('shoucang');
        let shoucang2 = localStorage.getItem('shoucang');
        props.shoucang.map((item, index) => {
            if (item == shoucang2) {
                setShow('block');
                setAppare('none');
            }else{
                setShow('none');
                setAppare('block');
            }
            return;
        })
    }
    useEffect (() => {
        compare();
    },[])
    const jump = () => {
        let flag = false;
        if (!localStorage.getItem('userinfo')) {
            props.history.push({
                pathname: './login',
                state: props.location
            })
        }

        if (localStorage.getItem('userinfo')) {
            // let shoucang = [];
            setShow('block');
            setAppare('none');
            localStorage.getItem('shoucang');
            let shoucang1 = localStorage.getItem('shoucang');

            props.dispatch({
                type: 'SHOU',
                value: shoucang1
            })
        }
    }
    return (

        <div className="bottom">
            <ul className="bottom-btn">
                <li className="bottom-btn1">
                    <div className="bottom-btn3"><img src="image/shop.png" /></div>
                    <p className="bottom-btn4">店铺</p>
                </li>
                <li className="bottom-btn1">
                    <div className="bottom-btn3"><img src="image/service.png" /></div>
                    <p className="bottom-btn4">客服</p>
                </li>
                <li className="bottom-btn1" onClick={jump}>
                    <div className="bottom-btn3">

                        <i className="iconfont icon-shoucang" style={{ display: isAppare }}></i>
                        <i className="iconfont icon-star__easyico" style={{ display: isShow }}></i>
                    </div>
                    <p className="bottom-btn4">收藏</p>
                </li>
                <li className="bottom-btn2"><button className="btn1">加入购物车</button></li>
                <li className="bottom-btn2"><button className="btn2">立即购买</button></li>
            </ul>
        </div>
    )
}

const mapStateToProps = (state) => ({
    shoucang: state.shouCangreducer,
    datalist: state.Bookreducer.datalist
})
export default connect(mapStateToProps)(Bottom)