let shoucang = [];
function shouCangreducer(state = shoucang, action) {
    switch (action.type) {
        case 'SHOU':
            // return { shoucang: action.shoucang };
            console.log("value:", action);
            return [...state, action.value];
        case 'DEL':
            let arr = [...state];
            arr.splice(action.index, 1);
            return arr;
        default:
            return state;
    }
}

export default shouCangreducer