const actions = () => {
    return (dispatch) => {
        let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist';
        fetch(url, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json'
            },
        }).then(res => res.json())
            .then(res => {
                dispatch({
                    type: 'INFORMATION',
                    datalist: res.data
                })
            });
    }
}
const collection = () => {
    return (dispatch) => {
        let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist';
        // let shoucang = [];
        let container = [];
        // let shoucang1 = [];
        fetch(url, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json'
            },
        }).then(res => res.json())
            .then(res => {
                for (let i = 0; i < res.data.length; i++) {
                    container.push(res.data[i].id);
                }
                dispatch({
                    type:'SHOU',
                    shoucang:container
                })
            })
    }
}
const turn = () => {
    return (dispatch) => {
        let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/login';
        fetch(url, {
            method: 'post',
            headers: {
                'Content-Type': 'application/json'
            },
        }).then(res => res.json())
            .then(res => {
                dispatch({
                    type: 'GET',
                    userinfo: res.data.name
                })
            });
    }
}

export { actions, turn, collection }