import React from 'react'
import { SearchBar} from 'antd-mobile';
class Search extends React.Component {
  state = {
    value: '美食',
  };
  onChange= (value) => {
    this.setState({ value });
  };
  clear = () => {
    this.setState({ value: '' });
  };
  render() {
    console.log(1);
    return (<div>
      <SearchBar
        value={this.state.value}
        placeholder="Search"
        onCancel={() => this.props.history.push('/')}
        showCancelButton 
        onChange={this.onChange}
      />
    </div>);
  }
}
export default Search