const Bookreducer = (state = { datalist: [] }, action) => {
    // let arr = [];
    switch (action.type) {
        case 'INFORMATION':
            // if(datalist!==[])
            return Object.assign({}, state, { datalist: action.datalist })
        default:
            return state;
    }
}


export default Bookreducer