import React, { Component } from 'react';
import Input from './Input';
import List from './List';

class TodoList extends Component {
    constructor(props) {
        super(props);
        this.state = {
            inpValue: '',
            todos: [{ title: '123', done: false }]
        }
    }
    handleChange = (e) => {
        this.setState({ inpValue: e.target.value })
        //监听内容与state里面的inpValue绑定起来
    }
    componentDidMount() {
        this.input.focus();
        let todos = localStorage.getItem('todos');
        if (todos) {
            this.setState({
                todos: JSON.parse(todos)
            })
        }
    }
    componentDidUpdate() {
        localStorage.setItem(
            'todos',
            JSON.stringify(this.state.todos)
        )
    }

    addTodo = () => {
        if (this.state.inpValue !== '') {
            this.setState({
                todos: [
                    {
                        title: this.state.inpValue,
                        done: false
                    },
                    ...this.state.todos
                ]
            })
        } else {
            return;
        }
        this.state.inpValue = '';
    }
    delButton = (idx) => {
        this.setState({
            //setState是异步调用
            todos: this.state.todos.filter((item, index) => index != idx)
        }, () => {
            localStorage.setItem(
                'todos',
                JSON.stringify(this.state.todos)
            )
        });
    }
    toggle = (index) => {
        let newTodos = this.state.todos  //利用深拷贝
        newTodos[index].done = !newTodos[index].done;
        this.setState({
            todos: newTodos
        })
    }
    render() {
        const { todos, inpValue } = this.state;
        return (
            <div>
                <Input addTodo={this.addTodo}
                    value={this.state.inpValue}
                    onChange={this.handleChange}
                    refs={inp => this.input = inp} />
                <List delButton={this.delButton}
                      toggle = {this.toggle}
                      todos = {this.state.todos}
                />
            </div>
        )
    }
}

export default TodoList