import React, { useEffect } from 'react'
//iconfont.cn 官网搜选图标，加入购物车
//点击页面右上角购物车，将图标添加进项目（没有项目就先点击“下载到本地”）
//解压压缩包，全部复制到项目public文件夹下，在index.html中通过link引入iconfont.css
//使用时，<i className = 'iconfont icon-具体名字'></i>
import Mytab from './Mytab'
import { connect } from 'react-redux'
import Lunbo from './Lunbo'
import './home.css'
import { actions } from './actionCreators'
import Container from './Container';
// import ContainerTest from './ContainerTest';
// let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist';
let datalist = [];

const Home = (props) => {
    //console.log("....",datalist);
    console.log("收藏夹：", props.shoucang);
    const getArry = () => {
        let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist';
        fetch(url, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json'
            },
        }).then(res => res.json())
            .then(res => {
                props.dispatch(actions(res.data));
            });
    }
    useEffect(() => {
        getArry();
    }, [])
    console.log("Home:", props.shoucang);
    return (
        <div className="big">
            <div className="title">
                <img className="taobao" src="image/taobao.png" />
                {/* <i className="tao"></i> */}
                <input type="text" placeholder="寻找宝贝店铺" onClick={() => props.history.push('/search')} style={{ marginLeft: "50px", width: "300px", marginTop: "5px", backgroundColor: "#ff4e22" }} />
            </div>
            <Lunbo />
            <Container />

            <Mytab />
        </div>
    );

}
const mapStateToProps = (state) => ({
    datalist: state.Bookreducer.datalist,
    shoucang: state.shouCangreducer
})
export default connect(mapStateToProps)(Home)