import React, { useEffect } from 'react'
import { connect } from 'react-redux'
import './home.css'
import { NavBar, Icon } from 'antd-mobile';
import Mytab from './Mytab'


const Cart = (props) => {
    useEffect(() => {
        if (!localStorage.getItem('userinfo')) {
            props.history.push({
                pathname: './login',
                state: props.location
            })
        }
    }, [])
    console.log("props.datalist：", props.datalist);
    console.log("Cart props.shoucang：", props.shoucang);
    let value = props.shoucang;
    console.log(value)
    // let midderValue = [];
    let middeValue = [];
    return (
        <div>
            <NavBar
                mode="light"
                icon={<Icon type="left" />}
                onLeftClick={() => props.history.goBack()}
                rightContent={[
                    <Icon key="0" type="search" style={{ marginRight: '16px' }} />,
                    <Icon key="1" type="ellipsis" />,
                ]}
            >收藏夹</NavBar>
            {
                props.shoucang.map((item, idx) => {
                    for (let i = 0; i < props.datalist.length; i++) {
                        if (item == props.datalist[i].id) {
                            middeValue.push(props.datalist[i]);
                        }
                    }
                    // for (let i = 0; i < props.shoucang.length; i++) {
                    // if (props.shoucang.includes(item.id)) {
                    //     console.log(item.id);
                    //     console.log("item2：", props.shoucang);

                    // }
                    console.log("middle:",middeValue);
                    return (
                        <li key={idx} className="cart-li">

                            <img className="cart-img" src={middeValue[idx].img} />

                            <button className="cart-btn" onClick={() => {
                                props.dispatch({
                                    type: "DEL",
                                    index: idx
                                })
                            }}>删除</button>
                        </li>
                    )
                })

            }
            <Mytab />
        </div>
    )
}

function mapStateToProps(state) {
    //得到state中的值
    return {
        userinfo: state.Loginreducer.userinfo,
        datalist: state.Bookreducer.datalist,
        shoucang: state.shouCangreducer
    }
}

export default connect(mapStateToProps)(Cart)