//export default createStore(Loginreducer)
let userinfo = '';
function Loginreducer(state={userinfo:''},action){
    switch(action.type){
        case 'GET':
            return {userinfo:action.userinfo};
        default :
            return state;
    }
}

export default Loginreducer
