import React from 'react';
import { TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: this.props.location.pathname,
            hidden: false,
            fullScreen: false,
        };
    }

    renderContent(pageText) {
        return (
            <div style={{ backgroundColor: 'white', height: '100%', textAlign: 'center' }}>
                <div style={{ paddingTop: 60 }}>Clicked “{pageText}” tab， show “{pageText}” information</div>
                <a style={{ display: 'block', marginTop: 40, marginBottom: 20, color: '#108ee9' }}
                    onClick={(e) => {
                        e.preventDefault();
                        this.setState({
                            hidden: !this.state.hidden,
                            //为真隐藏，为假不隐藏
                        });
                    }}
                >
                    Click to show/hide tab-bar
          </a>
                <a style={{ display: 'block', marginBottom: 600, color: '#108ee9' }}
                    onClick={(e) => {
                        e.preventDefault();
                        this.setState({
                            fullScreen: !this.state.fullScreen,
                        });
                    }}
                >
                    Click to switch fullscreen
          </a>
            </div>
        );
    }

    render() {
        const { pathname } = this.props.location;
        return (
            <div style={{ position: 'fixed', width: '100%', bottom: 0 }}>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="grey"
                    barTintColor="white"
                    hidden={this.state.hidden}
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={<i className="iconfont icon-Homehomepagemenu"></i>}
                        selectedIcon={
                            <i className="iconfont icon-Homehomepagemenu1"></i>
                        }
                        // selected={this.props.match.path === '/home'}
                        selected={pathname === '/home'}
                        //确定状态
                        onPress={() => {
                            this.props.history.push('/home')
                        }}
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            //在这里更改图片 <i styly={{}} className="iconfont icon-具体名字"></i>
                            <i className="iconfont icon-shoucang"></i>
                        }
                        selectedIcon={
                            <i className="iconfont icon-star__easyico"></i>
                        }
                        title="收藏"
                        key="Koubei"
                        selected={pathname === 'cart'}
                        onPress={() => {
                            this.props.history.push('/cart')
                        }}
                    >
                        {/* //类组件，所以要加this,函数组件的时候可以不加this */}
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className="iconfont icon-dingdan"></i>
                        }
                        selectedIcon={
                            <i className="iconfont icon-icon"></i>
                        }
                        title="订单列表"
                        key="Friend"
                        selected={pathname === '/list'}
                        onPress={() => {
                            this.props.history.push('/list')
                        }}
                    >

                    </TabBar.Item>
                    <TabBar.Item
                        icon={<i className="iconfont icon-my"></i>}
                        selectedIcon={<i className="iconfont icon-my1"></i>}
                        title="我的淘宝"
                        key="my"
                        selected={pathname === '/mytao'}
                        onPress={() => {
                            this.props.history.push('/mytao')
                        }}
                    >

                    </TabBar.Item>
                    <TabBar.Item
                        icon={<i className="iconfont icon-more"></i>}
                        selectedIcon={<i className="iconfont icon-more"></i>}
                        title="更多"
                        key="my"
                        selected={pathname === '/more'}
                        onPress={() => {
                            this.props.history.push('/more')
                        }}>

                    </TabBar.Item>
                </TabBar>
            </div>
        );
    }
}

export default withRouter(Mytab)