import React, { useEffect } from 'react'
import { HashRouter as Router, Switch, Route } from 'react-router-dom'
import Home from './Home'
import Cart from './Cart'
import List from './List'
import Mytao from './Mytao'
import More from './More'
import Search from './Search'
import Book from './Book'
import Book1 from './Book1'
import Book2 from './Book2'
import Book3 from './Book3'
import Book4 from './Book4'
import Book5 from './Book5'
import Book6 from './Book6'
import Book7 from './Book7'
import Book8 from './Book8'
import Book9 from './Book9'
import Login from './Login'
import store from './Store'
// import { actions } from './actionCreators'
//import store from './Loginreducer'
import { Provider } from 'react-redux'
import Init from './Init'
import Init2 from './Init2'


const App = () => {

    console.log("App.props:", store.getState());
    return (
        <Provider store={store}>
            <Router>
                <Switch>
                    <Route exact path="/" component={Init}/>
                    <Route path="/home" component={Home} />
                    <Route path="/cart" component={Cart} />
                    <Route path='/list' component={List} />
                    <Route path='/mytao' component={Mytao} />
                    <Route path='/more' component={More} />
                    <Route path='/search' component={Search} />
                    <Route path='/book' component={Book} />
                    <Route path='/book1' component={Book1} />
                    <Route path='/book2' component={Book2} />
                    <Route path='/book3' component={Book3} />
                    <Route path='/book4' component={Book4} />
                    <Route path='/book5' component={Book5} />
                    <Route path='/book6' component={Book6} />
                    <Route path='/book7' component={Book7} />
                    <Route path='/book8' component={Book8} />
                    <Route path='/book9' component={Book9} />
                    <Route path='/login' component={Login} />
                    <Route path='/init' component={Init2} />
                </Switch>
                {/* <Mytab /> */}
            </Router>
        </Provider>

    )
}

export default App