import React, { Component } from 'react'
class List extends Component {
    constructor(props) {
        super(props);
    }
    renderTodos = (done) => {
        return this.props.todos.map((todo, idx) => {
            if (done == todo.done) {
                return <li>
                    <input
                        onClick={() => this.props.toggle(idx)}
                        checked={done} type="checkbox" />
                    <span>{todo.title}</span>
                    <button onClick={() => this.props.delButton(idx)}>删除</button>
                    {/* ()=>this.delTodo(idx) 外面的小括号是事件处理函数，通过外面进行调用，里面才可以传参 */}
                </li>
            }
        })
    }
    render() {
        console.log(this.props);
        let tds = this.props.todos;
        let arr1 = tds.filter((item) => !item.done)
        let arr2 = tds.filter(item => item.done)
        return (
            <div>
                <h2>正在进行{arr1.length}</h2>
                <ul>
                    {this.renderTodos(false)}
                </ul>
                <h2>已经完成{arr2.length}</h2>
                <ul>
                    {this.renderTodos(true)}
                </ul>
            </div>
        )
    }
}

export default List