import React, { useEffect } from 'react'
import { connect } from 'react-redux'
import { actions,collection,turn } from './actionCreators'
// import {collection} from './actionCreators'
import Mytab from './Mytab'


const Init = (props) => {
    const getArry = () => {
        props.dispatch(actions());
        // props.dispatch(collection());
        props.dispatch(turn());
    }
    useEffect(() => {
        getArry();
    }, [])
    // console.log(props.datalist)
    console.log("看看初始值：",props.shoucang);
    if(props.datalist.length !==0){
        props.history.push('/home')
    }
    return (
        <div>
            请等待
            <Mytab />
        </div>
    )
}

const mapStateToProps = (state) => ({
    datalist: state.Bookreducer.datalist,
    shoucang:state.shouCangreducer.shoucang,
    userinfo : state.Loginreducer.userinfo
})
export default connect(mapStateToProps)(Init)
